import React from 'react'
import SwiperCore, { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.css';
//import { withRouter } from 'react-router-dom';
import LogoData from './LogoData'
import ArtData from './ArtData'

class Gallery extends React.Component {

  constructor(props) {
    super(props);
    SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);
    this.focus = new URLSearchParams(window.location.search).get("focus");
    this.path = window.location.pathname;

    if(this.path === "/art-gallery") {
      this.data = ArtData;
      this.show = ArtData.findIndex(l => l.name === this.focus);
    } else {
      this.data = LogoData;
      this.show = LogoData.findIndex(l => l.name === this.focus);
    }

    // no query param
    if(this.show === -1) {
      this.show = 0;
    }

    this.state = {
      slideIndex: this.show
    }

  }

  render() {
    const { history } = this.props;
    return (
      <div className="pure-g gallery-grid">
        <div className="pure-u-sm-1 pure-u-md-1-5 pure-u-lg-1-5"></div>
        <div className="pure-u-sm-1 pure-u-md-3-5 pure-u-lg-3-5">
          <div>
            <Swiper
              pagination={{ dynamicBullets: true, dynamicMainBullets: 4, clickable: true }}
              initialSlide={this.show}
              navigation
              onSlideChange={(slide) => {
                this.setState({ slideIndex: slide.activeIndex});
                //history.push(this.path + "?focus=" + this.data[slide.activeIndex].name);
                return true;
              }} >
              {this.props.logodata.map(logo => <SwiperSlide key={logo.name} className="gallery-image"><img className="pure-img" alt={logo.name} src={require(`${logo.image}`)}></img></SwiperSlide>)}
            </Swiper>
            <div className="centerme">
              <p>{this.props.logodata[this.state.slideIndex].name}</p>
            </div>
          </div>
          <div className="pure-u-sm-1 pure-u-md-1-5 pure-u-lg-1-5"></div>
        </div>
      </div>
    )
  }
}

//export default withRouter(Gallery);
export default Gallery;
